import AppBar from '@material-ui/core/AppBar'
import Button from '@material-ui/core/Button'
import Drawer from '@material-ui/core/Drawer'
import Hidden from '@material-ui/core/Hidden'
import IconButton from '@material-ui/core/IconButton'
import { makeStyles, useTheme } from '@material-ui/core/styles'
import Toolbar from '@material-ui/core/Toolbar'
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography'
import { Link } from 'gatsby'
import React from 'react'
import createPersistedState from 'use-persisted-state'

import LibraryBooksIcon from '@material-ui/icons/LibraryBooks'
import LocalOfferIcon from '@material-ui/icons/LocalOffer'
import GitHubIcon from '@material-ui/icons/GitHub'
import SvgIcon from '@material-ui/core/SvgIcon'
import SettingsIcon from '@material-ui/icons/Settings'
import MenuIcon from '@material-ui/icons/Menu'
import scrollbarStyle from '../styles/scrollbar'
// eslint-disable-next-line
// @ts-ignore
import pathList from '../sidebar.yaml'
import defaultSettings from '../lib/defaultSettings'
import Search from './Search'
import SiderContent from './Sidebar'
import Tabs from './Tabs'

const drawerWidth = 250

interface AppBar{
  background: string;
  color: string;
}
interface Props{
  appBar: AppBar;
}

const useStyles = makeStyles((theme) => ({
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  hiddenDrawer: {
    [theme.breakpoints.down('md')]: {
      display: 'none',
    },
  },
  appBar: (props: Props) => ({
    zIndex: theme.zIndex.drawer + 1,
    background: props.appBar.background,
    color: props.appBar.color,
  }),
  toolbar: {
    paddingLeft: '7.5px',
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('lg')]: {
      display: 'none',
    },
  },
  // necessary for content to be below app bar
  placeholder: {
    [theme.breakpoints.down('md')]: {
      minHeight: 64,
    },
    minHeight: 48 + 64,
    alignItems: 'flex-start',
  },
  drawerPaper: scrollbarStyle(theme, {
    width: drawerWidth,
  }),
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}))

function flattenObject (ob:any) :Record<string, unknown> {
  // https://stackoverflow.com/a/53739792
  const toReturn = {}

  for (const i in ob) {
    if (!Object.prototype.hasOwnProperty.call(ob, i)) continue

    if ((typeof ob[i]) === 'object' && ob[i] !== null) {
      const flatObject = flattenObject(ob[i])
      for (const x in flatObject) {
        if (!Object.prototype.hasOwnProperty.call(flatObject, x)) continue

        toReturn[i + '.' + x] = flatObject[x]
      }
    } else {
      toReturn[i] = ob[i]
    }
  }
  return toReturn
}

function getTabIDFromLocation (location: string, pathList: string[]): number {
  for (const v of Object.entries(pathList)) {
    if (Object.values(flattenObject(v[1])).indexOf(location) > -1) return +v[0]
  }
  return -1
}

interface drawerProps {
  pathname: string
}

const ResponsiveDrawer: React.FC<drawerProps> = (props) => {
  const { pathname } = props
  const [settings] = createPersistedState('settings')(defaultSettings)
  const theme = useTheme()
  const navColor = settings?.theme?.navColor !== 'auto' && typeof settings?.theme?.navColor !== 'undefined'
    ? settings.theme.navColor
    : theme.palette.background.paper // undefined or 'auto'
  const classes = useStyles({
    appBar: {
      background: navColor,
      color: theme.palette.getContrastText(navColor),
    },
  })
  const [mobileOpen, setMobileOpen] = React.useState(false)
  const OIWikiGithub = 'https://github.com/wgpsec/SafeWiki'
  const handleDrawerToggle = (): void => {
    setMobileOpen(!mobileOpen)
  }
  const tabID = getTabIDFromLocation(pathname, pathList)
  return (
    <>
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar className={classes.toolbar}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <Hidden mdDown implementation="css">
            <IconButton component={Link} color="inherit" to="/">
              <SvgIcon>
                <path d="M 6.046875 0 L 3.226562 7.894531 L 3.636719 8.3125 L 0 16.785156 L 6.355469 20.265625 L 7.085938 22.527344 L 9.582031 24 L 7.53125 16.46875 L 6.867188 18.40625 L 3.585938 16.574219 L 4.5625 15.269531 L 3 14.199219 L 4.097656 11.554688 L 4.675781 10.074219 L 6.558594 8 L 5.433594 6.691406 L 6.660156 3.085938 L 8.710938 5.230469 L 8.832031 6.550781 L 9.472656 6.6875 L 9.890625 7.269531 L 10.25 8.105469 L 11.988281 16.199219 L 13.789062 8.105469 L 14.070312 7.269531 L 14.59375 6.6875 L 15.246094 6.550781 L 15.257812 5.230469 L 17.363281 3.007812 L 18.535156 6.691406 L 17.449219 8 L 19.230469 10.085938 L 20.90625 14.117188 L 19.421875 15.214844 L 20.476562 16.574219 L 17.367188 18.40625 L 16.527344 16.46875 L 14.503906 24 L 17.195312 22.394531 L 17.730469 20.265625 L 24 16.835938 L 20.445312 8.210938 L 20.804688 7.738281 L 17.945312 0 L 13.710938 4.648438 L 10.480469 4.644531 Z M 6.046875 0 "/>
                <path d="M 6.550781 12.894531 L 7.304688 14.847656 L 9.113281 15.457031 L 10.019531 16.378906 L 9.542969 13.976562 Z M 6.550781 12.894531 "/>
                <path d="M 17.382812 12.894531 C 17.316406 12.910156 14.410156 13.941406 14.410156 13.941406 L 13.863281 16.378906 L 14.90625 15.527344 L 16.734375 14.8125 Z M 17.382812 12.894531 "/>
                <path d="M 10.636719 23.117188 L 11.988281 24 L 13.375 22.980469 L 13.804688 21.199219 L 12.839844 22.339844 L 11.199219 22.351562 L 10.175781 21.199219 Z M 10.636719 23.117188 "/>
              </SvgIcon>
            </IconButton>
          </Hidden>
          <Button href="/" color="inherit">
            <Typography variant="h6" noWrap>
              WgpSec SafeWiki
            </Typography>
          </Button>
          <div style={{ flexGrow: 1 }} />
          <Search />
          <Tooltip title="" placement="bottom" arrow>
            <IconButton component="a" href="/settings" color="inherit">
              <SettingsIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="" placement="bottom" arrow>
            <IconButton component="a" href="/tags" color="inherit">
              <LocalOfferIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="" placement="bottom" arrow>
            <IconButton component="a" href="/pages" color="inherit">
              <LibraryBooksIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="GitHub " placement="bottom" arrow>
            <IconButton component="a" href={OIWikiGithub} color="inherit">
              <GitHubIcon />
            </IconButton>
          </Tooltip>
        </Toolbar>
        <Hidden mdDown implementation="css">
          <Tabs tabID={tabID >= 0 ? tabID : 0} pathList={pathList}/>
        </Hidden>
      </AppBar>
      <Hidden lgUp implementation="js">
        <Drawer
          variant="temporary"
          anchor={theme.direction === 'rtl' ? 'right' : 'left'}
          open={mobileOpen}
          onClose={handleDrawerToggle}
          classes={{
            paper: classes.drawerPaper,
          }}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
        >
          <SiderContent pathList={pathList} {...props} />
        </Drawer>
      </Hidden>
      <Hidden mdDown implementation="css">
        <Drawer
          className={classes.drawer}
          classes={{
            paper: classes.drawerPaper,
          }}
          variant="permanent"
          open
        >
          <div className={classes.placeholder} />
          <SiderContent pathList={tabID !== -1 ? [pathList[tabID]] : pathList} {...props} />
        </Drawer>
      </Hidden>
    </>
  )
}

export default ResponsiveDrawer
