import lightBlue from '@material-ui/core/colors/lightBlue'
import Grid from '@material-ui/core/Grid'
import Link from '@material-ui/core/Link'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import { graphql, StaticQuery } from 'gatsby'
// import React, { useEffect } from 'react'
import React from 'react'

const useStyles = makeStyles((theme) => ({
  link: {
    color: theme.palette.footer.text,
    '&:hover': {
      textDecoration: 'none',
      color: lightBlue[500],
    },
  },
}))

const query = (props) => (
  <StaticQuery
    query={graphql`
      query lastestCommit {
        allGitCommit(limit: 1) {
          nodes {
            hash
            date
          }
        }
      }
    `}
    render={(data) => <FooterContent data={data} {...props} />}
  />
)
export default query

// let easteregg = false

function FooterContent ({ data }) {
  const classes = useStyles()
  const { date, hash } = data.allGitCommit.nodes[0]

  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Typography>
          Copyright  {date.substr(0, 4)} WgpSec Team
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <Typography>
          :{' '}
          {hash.substr(0, 7)} {' '}
          {date.substr(0, 10)}
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <Typography>
          
          <Link className={classes.link} href="https://wgpsec.org">
            WgpSec 
          </Link>
        </Typography>
      </Grid>
    </Grid>
  )
}
